body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace; }

.nav {
  background: #fff;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.3); }
  .nav .navbar {
    padding: 10px 0; }
    .nav .navbar .navbar-brand {
      font-weight: 700;
      font-size: 25px;
      cursor: pointer; }

.login-screen {
  box-shadow: 0 0 5px rgba(121, 121, 121, 0.3);
  margin: 20px auto;
  width: 300px;
  background-color: #FFF;
  padding: 20px;
  border-radius: 5px; }

.app-title {
  text-align: center;
  color: #777; }

.login-form {
  text-align: center; }

.form-control:focus {
  box-shadow: none;
  outline: none; }

.parentCart {
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
  border-radius: 5px;
  background: #fff;
  overflow: hidden;
  margin-bottom: 20px; }
  .parentCart img {
    width: 100%;
    height: 200px; }
  .parentCart .details {
    position: relative; }
    .parentCart .details h5 {
      font-size: 22px;
      color: #252222;
      text-transform: capitalize;
      font-weight: 600; }
    .parentCart .details p {
      font-size: 16px;
      color: #707070;
      margin-bottom: 5px;
      text-transform: capitalize;
      font-weight: 600; }
    .parentCart .details .btn {
      position: absolute;
      right: 15px;
      bottom: 18px;
      background: #fff;
      padding: 0 0;
      font-size: 20px;
      color: #707070; }
    .parentCart .details .btn:focus {
      box-shadow: none;
      outline: none; }

.parent {
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
  border-radius: 5px;
  background: #fff;
  overflow: hidden;
  margin-bottom: 20px; }
  .parent img {
    width: 100%;
    height: 200px; }
  .parent .details {
    padding: 15px;
    position: relative; }
    .parent .details h5 {
      font-size: 22px;
      color: #252222;
      text-transform: capitalize; }
    .parent .details p {
      font-size: 16px;
      position: absolute;
      color: #707070;
      right: 15px;
      bottom: 3px; }
    .parent .details .btn {
      background: #fff;
      padding: 0 0;
      font-size: 14px;
      color: #252222;
      text-transform: capitalize; }
      .parent .details .btn img {
        width: 30px;
        height: 30px; }
    .parent .details .btn:focus {
      box-shadow: none;
      outline: none; }

footer {
  background: #000;
  padding: 50px 0; }
  footer ul.items {
    display: inline-flex;
    margin-bottom: 0; }
    footer ul.items li {
      margin: 0 15px; }
      footer ul.items li a,
      footer ul.items li a:hover {
        text-transform: capitalize;
        text-decoration: none;
        color: #fff; }
  footer ul.social {
    padding: 20px 0 0 0;
    margin-bottom: 0; }
    footer ul.social li {
      margin: 0 30px;
      display: inline-flex; }
      footer ul.social li a {
        text-decoration: none;
        color: #707070; }

.product img {
  width: 100%;
  height: 250px;
  border-radius: 5px; }

.product h5 {
  font-size: 22px;
  color: #252222;
  text-transform: capitalize;
  font-weight: 600; }

.product p {
  font-size: 16px;
  color: #707070;
  margin-bottom: 5px;
  text-transform: capitalize;
  font-weight: 600; }

.cartIcon img {
  width: 35px;
  height: 35px;
  cursor: pointer; }
  .cartIcon img span {
    font-size: 12px;
    color: #fff; }

header {
  background: #1daaa3;
  padding: 10px 0; }
  header h4 {
    font-size: 14px;
    margin-bottom: 0;
    color: #fff; }
    header h4 span {
      margin-left: 5px; }

